.link-icon {
    display: flex;
    column-gap: 8px;

    &__icon {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: $color-default-white;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__text {
        font-size: 15px;
        color: $color-night-rider;

        @include vp--1023 {
            display: none;
        }
    }
}